@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  font-family: 'Abel';
}

.App {
  text-align: center;
}

.page {
  background-color: #3b4d61;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.display_Grid__Q0L1s {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  width: 100%;
}

/* Buzz */
/* buzz code adjusted a bit, but directly from: https://ianlunn.github.io/Hover/ */
@-webkit-keyframes display_hvr-buzz__2EqNT {
  50% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  100% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
}
@keyframes display_hvr-buzz__2EqNT {
  50% {
    -webkit-transform: translateX(3px) rotate(2deg);
    transform: translateX(3px) rotate(2deg);
  }
  100% {
    -webkit-transform: translateX(-3px) rotate(-2deg);
    transform: translateX(-3px) rotate(-2deg);
  }
}
.display_BuzzHover__2zJ76 {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.display_BuzzHover__2zJ76:hover, .display_BuzzHover__2zJ76:focus, .display_BuzzHover__2zJ76:active {
  color: yellow;
  cursor: pointer;
  -webkit-animation-name: display_hvr-buzz__2EqNT;
  animation-name: display_hvr-buzz__2EqNT;
  -webkit-animation-duration: 0.15s;
  animation-duration: 0.15s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.numberBox_numberBox__KcH8l {
  color: aquamarine;
  background-color: #3b4d61;
}

